export function FetchSentNominations(username) {
    const host = process.env.REACT_APP_BACKEND_HOST;
    return fetch(`${host}/form/nominator/?name=${username}`)
    .then(response=> response.json())
    .then(data=> {
       return data;
    })
    .catch(error=> 
    window.Materialize.toast(error, 2000)   
    );
}
export function FetchRecievedNominations(username) {
    const host = process.env.REACT_APP_BACKEND_HOST;
    return fetch(`${host}/form/nominee/?name=${username}`)
    .then(response=> response.json())
    .then(data=> {
       return data;
    })
    .catch(error=> 
    window.Materialize.toast(error, 2000)   
    );
}