const host = process.env.REACT_APP_BACKEND_HOST;

export const login = (() => {
    return document.cookie.indexOf('login=') === -1 ? (
         fetch(`${host}/auth/`,{ method: 'GET' })
        .then(response => response.json())
        .then(response => window.location.href = response.authurl)
        .catch((err) => console.log("Error trying to log, please try again",err))
    ) : null
});
