import React, { Component } from 'react'
import { connect } from 'react-redux';
import moment from 'moment';
import { bindActionCreators } from 'redux';
import actions from './actions';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import {login} from './utils/login';
import Cookies from 'universal-cookie';
import Auth from './components/auth/auth';
import Landing from './components/landing/landing';
import NavBar from './components/NavBar/NavBar';
import Nomination from './components/nomination/nomination';
import Reports from './components/reports/reports';
import './App.css'

const COOKIE = new Cookies();

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      nominationSuccess: false,
      logged: false 
    }
    this.formStatus = this.formStatus.bind(this);
    this.setCookie = this.setCookie.bind(this);
  }

  formStatus(status) {
    this.setState({
      nominationSuccess : status
    });
  }

  setCookie(value) {
    const DATE = new Date(moment().add(1,'hour').format());
    COOKIE.set("login", value, { path: '/' , expires: DATE });
    this.setState({
      logged: true
    });
  }

  componentDidMount() {
    login();
  }
  
  render() {
    const { isUserLogged } = this.props;
    return (
      <Router basename={process.env.REACT_APP_PUBLIC_URL}>
        <div className="app">
          {isUserLogged ? <NavBar/> : null}
          <div id="content">
            <Switch>
              <Route exact path="/" render={props => (
                  <Landing nominationSuccess={this.state.nominationSuccess} />
              )} />
              <Route exact path="/auth" render={props => (
                  <Auth setCookie={this.setCookie} />
              )}/>
              <Route exact path="/stats" component={Reports} />
              <Route exact path="/nominate" render={props => (
                <Nomination formStatus={this.formStatus} />
              )} />
              <Route exact path="/help" component={Landing} />
              <Route component={NoMatch} />
            </Switch>  
          </div>
        </div>
      </Router>   
    );
  }
}

const mapStateToProps = (state => {
  return { isUserLogged: state.login.isUserLogged }
});

const mapDispatchToProps = (dispatch => {
  return { actions: bindActionCreators(actions, dispatch), dispatch }
});

const NoMatch = ({ location }) => (
  <div>
    <h3>
      <p>Error 404 <code>{location.pathname}</code> Not Found </p>
    </h3>
  </div>
);

export default connect(mapStateToProps, mapDispatchToProps)(App);
