import React, { Component } from 'react';
import { Row, Col, Button } from 'react-materialize';
import { ProgressBar } from 'react-materialize';
import { Link } from 'react-router-dom';
import Cookies from 'universal-cookie';
import Medal from '../medal/medal';
import './landing.css';

/* KUDOS BADGES */
import club from '../../assets/images/kudos/club.svg';
import diamond from '../../assets/images/kudos/diamond.svg';
import novice from '../../assets/images/kudos/novice.svg';
import ace from '../../assets/images/kudos/ace.svg';
import star from '../../assets/images/kudos/star.svg';

const COOKIE = new Cookies();

class Landing extends Component {

  constructor(props) {
    super(props);

    this.state = {
      kudos: [],
      loaded: false
    }
    this.formStatus = this.formStatus.bind(this);
    this.setKudos = this.setKudos.bind(this);
    this.getKudosBadges = this.getKudosBadges.bind(this);
    this.kudosLoad = this.kudosLoad.bind(this);
  }

  componentWillMount() {
    if (COOKIE.get("login") !== undefined) {
      /* TO-DO on Gamification Strategy */
      //this.displayNextLevelInfo();
    }
    this.formStatus();
  }

  formStatus() {
    if (this.props.nominationSuccess) {
      return window.Materialize.toast('Your Nomination has been <span>successfully sent</span><i class="material-icons">check</i>', 3000, 'msgToast');
    }
  }

  setKudos(kudos){
    this.setState({
      kudos: kudos,
      loaded: true
    });
  }
  
  getKudosBadges(badge) {
    let kudos = this.state.kudos;  
    if(kudos.indexOf(badge) === -1)
      return "badge-disabled";
  }

  kudosLoad() {
    if(this.state.loaded !== false) {
      return( 
        <Row className="home_kudos">
          <Col className="badges">
            <h3>My Kudos Badges</h3>
            <div class="wrapper">
              <img src={ace} className={this.getKudosBadges("ace")}/>
              <div class="tooltip"><span>CVA Received:</span> Receive 1 nomination</div>
            </div>
            <div class="wrapper">
              <img src={novice} className={this.getKudosBadges("novice")}/>
              <div class="tooltip"><span>Novice Nominator:</span> Make 1 nomination</div>
            </div>
            <div class="wrapper">
              <img src={club} className={this.getKudosBadges("club")} />
              <div class="tooltip"><span>Club:</span> 3 awards for the same value</div>
            </div>
            <div class="wrapper">
              <img src={star} className={this.getKudosBadges("star")}/>
              <div class="tooltip"><span>Super Star:</span> 5 awards for the same value</div>
            </div>
            <div class="wrapper">
              <img src={diamond} className={this.getKudosBadges("diamond")}/>
              <div class="tooltip"><span>Diamond:</span> Earn all 4 Core Values</div>
            </div>
          </Col>
        </Row>
      );
    }
  }

  /* TO-DO on Gamification Strategy */
  /*
  displayNextLevelInfo() {
    window.Materialize.toast('<span class="number">9</span> more nomination(s) for <span class="level">extreme nominator level</span><i class="material-icons">announcement</i>', 3000, 'nominationsToast');
  }
  */

  render() {
    return (
      <div className="home">
        <Row className="home_header">
          <Col s={12}>
            <h1>Intersys <span>Core Values</span> Awards</h1>
          </Col>
          <Col s={12}>
            <Link to="/nominate"><Button waves='light'>Nominate Now</Button></Link>
          </Col>
        </Row>
        <Row className="home_content">
          <Col className="medals">
            <Medal setKudos={this.setKudos} type={"Given"} />
            <Medal type={"Received"} />
          </Col>
        </Row>
        {this.kudosLoad()}        
      </div>
    );
  }
}

export default Landing;
