import React, { Component } from 'react'
import { ProgressBar } from 'react-materialize';
import Cookies from 'universal-cookie';
import { FetchSentNominations, FetchRecievedNominations } from '../../utils/helpers';
import './medal.css';

/* IMPORTING MEDALS */
import noGiven from '../../assets/images/awards/noGiven.svg';
import noReceived from '../../assets/images/awards/noReceived.svg';
import blue from '../../assets/images/awards/blue.svg';
import bronze from '../../assets/images/awards/bronze.svg';
import silver from '../../assets/images/awards/silver.svg';
import gold from '../../assets/images/awards/gold.svg';
import platinum from '../../assets/images/awards/platinum.svg';
import contributor from '../../assets/images/awards/contributor.svg';
import cheerleader from '../../assets/images/awards/cheerleader.svg';
import coach from '../../assets/images/awards/coach.svg';
import captain from '../../assets/images/awards/captain.svg';
import champion from '../../assets/images/awards/champion.svg';

/* MEDALS DICTIONARY (name: path) */
const MEDALS = {
  noGiven: noGiven,
  noReceived: noReceived,
  blue: blue,
  bronze: bronze,
  silver: silver,
  gold: gold,
  platinum: platinum,
  contributor: contributor,
  cheerleader: cheerleader,
  coach: coach,
  captain: captain,
  champion: champion
};

const TOOLTIP = {
  content: {
    background: 'transparent',
    padding: '0',
  },
  tooltip: {
    background: 'rgba(0,0,0,0.7)',
    border: '2px solid #f88f3d',
    borderRadius: '30px',
    padding: '10px'
  },
  arrow: {
    borderTop: 'solid #f88f3d 5px',
  },
};

const COOKIE = new Cookies();

class Medal extends Component {

  constructor() {
    super();
    this.state = {
      nominations: [],
      loaded: false,
      badges: {
        awarded: {
          image_path: '',
          name: ''
        },
        earned: {
          image_path: '',
          name: ''
        }
      },
      nextBadges: {
        awarded: {
          count: 0,
          next: ''
        },
        earned: {
          count: 0,
          next: ''
        }
      }
    };
    this.getAwards = this.getAwards.bind(this);
  }

  getAwards() {
    const host = process.env.REACT_APP_BACKEND_HOST;
    return fetch(`${host}/user/gamification/${COOKIE.get("login").email}`, {
      method: 'GET',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
        /*'Authorization': `Bearer ${COOKIE.get("login").token}`*/
      }
    })//end of fetch
      .then(response => response.json())
      .then(awards => {
        let earned = awards.badges.earned;
        let earnedPath = earned;
        let awarded = awardedPath =awards.badges.awarded;
        let awardedPath = awarded;
        let nextEarned = awards.nextBadges.earned.next;
        let nextEarnedCount = awards.nextBadges.earned.count;
        let nextAwarded = awards.nextBadges.awarded.next;
        let nextAwardedCount = awards.nextBadges.awarded.count;
        let kudos = awards.kudos;

        if(this.props.type === "Given")
          this.props.setKudos(kudos);

        if(earnedPath === "")
        earnedPath = "noReceived";
        
        if(awardedPath === "")
        awardedPath = "noGiven";
        
        this.setState({
          badges: {
            awarded: {
              image_path: MEDALS[awardedPath],
              name: awarded
            },
            earned: {
              image_path: MEDALS[earnedPath],
              name: earned
            }
          },
          nextBadges: {
            awarded: {
              count: nextAwardedCount,
              next: nextAwarded
            },
            earned: {
              count: nextEarnedCount,
              next: nextEarned
            }
          }
        });
      })      
      .catch((json) => {
        console.log("No Awards Received");
      });
  }

  componentDidMount() {
    if (COOKIE.get("login") !== undefined) {
      if (this.props.type === 'Given') {
        FetchSentNominations(COOKIE.get("login").email).then(res => {
          this.setState({
            nominations: res,
            loaded: true
          })
        });
      } else {
        FetchRecievedNominations(COOKIE.get("login").email).then(res => {
          this.setState({
            nominations: res,
            loaded: true
          })
        })
      }
    }
    this.getAwards();
  }

  render() {
    const medals = this.state.nominations;
    const awardedMedal = this.state.badges.awarded;
    const earnedMedal = this.state.badges.earned;
    const nextAwarded = this.state.nextBadges.awarded;
    const nextEarned = this.state.nextBadges.earned;

    let currentMedal, nextMedal = "";
    
    if (this.props.type === "Given") {
      currentMedal = awardedMedal;
      nextMedal = nextAwarded;
    }
    else {
      currentMedal = earnedMedal;
      nextMedal = nextEarned;
    }
    
    if (this.state.loaded === false) {
      return (
        <div>
          <ProgressBar />
        </div>
      );
    } 
    else {
      return (
        <div class="wrapper medal-wrapper">
          <div className={"medal " + this.props.type.toLocaleLowerCase() + "_medal"}>
            <h1>Awards {this.props.type}: <span className="number">{medals.length}</span></h1>
            <img src={currentMedal.image_path}/>
          </div>
          <div class="tooltip">
            <div className="medal_tooltip">
              <p className="text">{currentMedal.name !=="" ? "You currently earned a" : ""}</p>
              <p className="level">{currentMedal.name !=="" ? currentMedal.name + " badge" : ""}</p>
              <p className="number">{nextMedal.count !== 0 ? nextMedal.count : ""}</p>
              <p className="text">{nextMedal.count > 1 ? "more nominations to" : nextMedal.count !== 0 ? "more nomination to" : "the highest level, Congratulations!"}</p>
              <p className="levelNext">{nextMedal.count !== 0 ? nextMedal.next + " badge" : ""}</p>
            </div>
         </div>
        </div>
      );
    }
  }
}

export default Medal