import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import Cookies from 'universal-cookie';
import { Navbar, Icon, Dropdown } from 'react-materialize';
import {login} from '../../utils/login';
import NavItem from '../navItem/navItem';
import MyActivity from '../MyActivity/MyActivity';
import Logo from '../../assets/images/intersys-logo.svg';
import './NavBar.css';

const cookie = new Cookies();

const mapStateToProps = (state => {
  return {
    isUserLogged: state.login.isUserLogged
  }
});

class NavBar extends Component {
  constructor(props) {
    super(props);
  }

  isUserAdmin() {
    return cookie.get("login") && cookie.get("login").usertype === 'admin' ? <NavItem href='/stats'>Stats</NavItem> : null;
  }

  signOutSession(event) {
    event.preventDefault();
    cookie.remove("login",{path: '/'});
    login();
  }

  render() {
    const {isUserLogged} = this.props;
    const logo = <Link to="/"><img href="/" className="logo" src={Logo} /></Link>;
    
    return isUserLogged ? (
      <Navbar brand={logo} right className='navbar-intersys'>
        <NavItem href='/'><Icon>home</Icon></NavItem>
        <NavItem href='/nominate'>Nominate</NavItem>
        <MyActivity />
        {this.isUserAdmin()}
        <Dropdown trigger={<NavItem className="session-menu">{cookie.get("login") && cookie.get("login").name}<i class="material-icons right">keyboard_arrow_down</i></NavItem>}>
          <li className="help"><a href="https://intersysconsulting.slack.com/app_redirect?channel=recognition-app-help" target="_blank">help<i class="material-icons right">help</i></a></li>
          <li class="sign-out" onClick={this.signOutSession}><p>Sign Out <i class="material-icons right">power_settings_new</i></p></li>
        </Dropdown>
      </Navbar>
    ) : null;
  }
}

export default connect(mapStateToProps,null)(NavBar);
